/* Container for the chatbot */
.chatbot {
  width: 360px;
  border-radius: 15px;
  background: linear-gradient(135deg, #f5f7fa 0%, #c3cfe2 100%);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  margin: 40px auto;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  overflow: hidden;
}

/* Container for messages */
.chatbot-messages {
  height: 400px;
  overflow-y: auto;
  padding: 15px;
  background-color: #ffffff;
  border-bottom: 1px solid #e0e0e0;
}

/* Individual message style */
.chatbot-message {
  margin-bottom: 15px;
  padding: 10px 15px;
  border-radius: 20px;
  max-width: 75%;
  word-wrap: break-word;
  font-size: 14px;
  line-height: 1.4;
}

/* User message style */
.chatbot-message.user {
  background-color: #dcedf7;
  color: #333;
  margin-left: auto;
  text-align: right;
}

/* Bot message style */
.chatbot-message.bot {
  background-color: #f0f0f0;
  color: #333;
  margin-right: auto;
  text-align: left;
}

/* Input container style */
.chatbot-input {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  padding: 10px 15px;
  border-top: 1px solid #e0e0e0;
}

/* Input field style */
.chatbot-input input {
  flex: 1;
  padding: 10px 15px;
  border-radius: 20px;
  border: 1px solid #d0d0d0;
  outline: none;
  font-size: 14px;
  transition: border 0.3s ease;
}

.chatbot-input input:focus {
  border-color: #007bff;
}

/* Send button style */
.chatbot-input button {
  margin-left: 10px;
  padding: 10px 20px;
  background-color: #007bff;
  color: #ffffff;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s ease;
}

.chatbot-input button:hover {
  background-color: #0056b3;
}

/* Scrollbar styling for messages */
.chatbot-messages::-webkit-scrollbar {
  width: 6px;
}

.chatbot-messages::-webkit-scrollbar-track {
  background: #f1f1f1;
  border-radius: 10px;
}

.chatbot-messages::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

.chatbot-messages::-webkit-scrollbar-thumb:hover {
  background: #555;
}
